<template>
    <div>
      <Tree :data="baseData" show-checkbox  @on-select-change="a"></Tree>
    </div>
</template>
<script>
  import Tree from 'iview/src/components/tree';
  export default {
    components:{
      Tree
    },
    data () {
      return {
        baseData: [{
          expand: true,
          title: 'parent 1',
          children: [
            {
            title: 'parent 1-0',
            expand: true,
            children: [{
              title: 'c',
            }, {
              title: 'b',
            }]
           },
            {
            title: 'parent 1-1',
            expand: true,
            children: [{
              title: 'a',
            }]
          }]
        }]
      }
    },
    methods:{
      a(val){
        console.log(val)
      }
    }
  }
</script>



